import logo from './logo.svg';
import './App.css';
import {BrowserRouter as Router,Routes,Route} from 'react-router-dom'
 
import NavBar from './Components/NavBar';
import Home from './Pages/Home';
import LoginPage from './Pages/LoginPage';
import FormAddClients from './Components/FormAddClients';
import { createContext, useEffect, useState } from 'react';
import TransferMoney from './Components/TransferMoney';
import TransHistory from './Components/TransHistory';
import TransAllHistory from './Components/TransAllHistory';
import AllUsers from './Components/AllUsers';
import Balance from './Components/Balance';
import SingleInfoForUpdate from './Components/SingleInfoForUpdate';

 
function App() {
  const [dataUser, setdataUser] = useState({})
  useEffect(() => {
    localStorage.getItem('userInfo') && setdataUser(JSON.parse(localStorage.getItem('userInfo')))
  }, [])
  console.log(dataUser);
  return (
     
    <Router >
    <NavBar />
    <Routes>
 <Route path="/" element={<Home/>}/>
 <Route path={`/login`}  element={<LoginPage/>}/>
 <Route path="/add/clients"  element={<FormAddClients/>}/>
 <Route path="/transuction/History"  element={<TransHistory/>}/>
 <Route path="/transferMoney"  element={<TransferMoney/>}/>
 <Route path="/TransAllHistory"  element={<TransAllHistory/>}/>
 <Route path="/AllUser"  element={<AllUsers/>}/>
 <Route path="/balance"  element={<Balance/>}/>
 <Route path="/update/info/:id"  element={<SingleInfoForUpdate/>}/>
   
    </Routes>
    
    </Router>
   
  );
}

export  default App
